<button
	class="to-top"
	on:click={$milk.browser.scrollToTop}
	title="Link to top of page."
>
	<span class="top-link"><span><slot>Back To Top</slot></span></span>
</button>

<script>/* ## MILK ## */
import { milk } from '$milk/milk.js';</script>

<style>.to-top{display:block;cursor:pointer;border:0;padding:0;margin:0 auto;background:none}.to-top:active,.to-top:focus{outline:none}.top-link{pointer-events:none;cursor:pointer}</style>
